import { useEffect, useState } from "react";
import ReactGA from "react-ga";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faLinkedin, faGithubSquare } from "@fortawesome/free-brands-svg-icons";
import Calculator from "./views/Calculator";
import NewCalculator from "./views/NewCalculator";
import Result from "./views/Result";
import { t, setLocale } from "./utils/translate";
import { useJitsu } from "@jitsu/jitsu-react";
import { identifyUser } from "./utils/identity";
import "./assets/css/app.css";

library.add(faLinkedin, faGithubSquare);

const App = () => {
  const [view, setView] = useState(0); // 0: form, 1: result
  const [activeCalc, setActiveCalc] = useState("new"); // 'old' or 'new'
  const [oldState, setOldState] = useState({
    price: 0,
    years: 0,
    interest: "i3",
    rentType: "fixed",
    serviceType: "in",
    advance: 0,
    advPercent: "",
  });

  const [results, setResults] = useState({
    rent: 0,
    totalUnit: 0,
    totalCash: 0,
    totalLoan: 0,
    totalInterest: 0,
    totalPaid: 0,
    minSalary: 0,
  });

  const [newState, setNewState] = useState({
    price: 0,
    years: 0,
    interestRate: 8,
    downPercent: 20,
    advance: 0,
  });

  // Handle language (ar / en) with persistence
  const [language, setLanguage] = useState(
    localStorage.getItem("lang") || "ar"
  );

  // Ensure translator has correct locale on first render
  setLocale(language);

  const { analytics } = useJitsu() || {};

  // Identify anonymous user once analytics is ready
  useEffect(() => {
    identifyUser(analytics);
  }, [analytics]);

  useEffect(() => {
    ReactGA.initialize("G-11HKFLJ56F");
  }, []);

  // Persist language selection in localStorage when it changes
  useEffect(() => {
    localStorage.setItem("lang", language);
  }, [language]);

  return (
    <div className="App" dir={language === "ar" ? "rtl" : "ltr"}>
      <div className="bg-image" />
      <div className="layout">
        <div className="calc-container">
          <h2>{t("appTitle")}</h2>
          <div className="lang-switch text-right">
            <button
              className="lang-btn"
              onClick={() =>
                setLanguage((prev) => (prev === "ar" ? "en" : "ar"))
              }
            >
              {language === "ar" ? "EN" : "AR"}
            </button>
          </div>
          <hr />
          {/* Tabs */}
          <div className="tabs flex mb-2">
            <button
              className={`tab-btn ${activeCalc === "old" ? "active" : ""}`}
              onClick={() => {
                setActiveCalc("old");
                setView(0);
              }}
            >
              {t("oldCalcTab")}
            </button>
            <button
              className={`tab-btn ${activeCalc === "new" ? "active" : ""}`}
              onClick={() => {
                setActiveCalc("new");
                setView(0);
              }}
            >
              {t("newCalcTab")}
            </button>
          </div>

          {view === 0 && activeCalc === "old" && (
            <Calculator
              state={oldState}
              setState={setOldState}
              setResults={setResults}
              viewHandler={setView}
            />
          )}

          {view === 0 && activeCalc === "new" && (
            <NewCalculator
              state={newState}
              setState={setNewState}
              resultsHandler={setResults}
              viewHandler={setView}
            />
          )}

          {view === 1 && (
            <Result
              results={results}
              viewHandler={setView}
              serviceCash={
                activeCalc === "old" && oldState.serviceType === "out"
              }
              legacyResults={activeCalc === "old"}
            />
          )}
        </div>
      </div>
      <div className="footer">
        <div className="about-text text-center">
          <small style={{ color: "darkred" }}>{t("shariaaDisclaimer")}</small>
          <br />
          <small>{t("aboutText")}</small>
        </div>
        <p>
          {t("developedBy")}
          <a
            href={window.atob(
              "aHR0cHM6Ly93d3cubGlua2VkaW4uY29tL2luL2FiZGVscmFobWFuYWJkZWxoYW1pZC8="
            )}
            target="_blank"
            rel="noreferrer"
            className="mr-1"
          >
            {t("developerName")}
          </a>
        </p>
        <div className="icons">
          {/* <a className="youtube" href={window.atob('aHR0cHM6Ly93d3cueW91dHViZS5jb20vYy9EZXZUYXJlaw==')} target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faYoutube} />
          </a> */}
          <a
            className="github"
            href={window.atob("aHR0cHM6Ly9naXRodWIuY29tL2Rldi10YXJlaw==")}
            target="_blank"
            rel="noreferrer"
          >
            <FontAwesomeIcon icon={faGithubSquare} />
          </a>
          <a
            className="linkedin"
            href={window.atob(
              "aHR0cHM6Ly93d3cubGlua2VkaW4uY29tL2luL2FiZGVscmFobWFuYWJkZWxoYW1pZC8="
            )}
            target="_blank"
            rel="noreferrer"
          >
            <FontAwesomeIcon icon={faLinkedin} />
          </a>
        </div>
      </div>
    </div>
  );
};

export default App;
