import FormattedNumber from './FormattedNumber'

const TableRow = ({ label, value, help, color }) => (
  <tr style={color ? { color } : undefined}>
    <td>
      {label}
      {help && <small className="help"> {help}</small>}
    </td>
    <td>
      {Array.isArray(value)
        ? value.map((v, index) => (
            <p key={index} className="m-0">
               ({index + 1}): <FormattedNumber className="mr-1" value={v} />
            </p>
          ))
        : <FormattedNumber value={value} />}
    </td>
  </tr>
)

export default TableRow 