import { useState } from 'react'
import NumberFormat from 'react-number-format'
import * as yup from 'yup'

import { calculateAdvance } from '../utils/helpers'
import { t } from '../utils/translate'
import { useJitsu } from '@jitsu/jitsu-react'

const NewCalculator = ({ state, setState, resultsHandler, viewHandler }) => {
  const [errors, setErrors] = useState({})
  const { analytics } = useJitsu() || {}

  const schema = yup.object().shape({
    price: yup.number()
      .typeError(t('typeError'))
      .required(t('requiredPrice'))
      .positive(t('positiveError'))
      .min(100000, t('priceRangeErrorNew')),
    years: yup.number()
      .typeError(t('typeError'))
      .required(t('requiredYears'))
      .positive(t('positiveError'))
      .integer(t('integerError'))
      .max(30, t('yearsRangeError')),
    interestRate: yup.number()
      .typeError(t('typeError'))
      .required(t('requiredInterest'))
      .min(0),
    downPercent: yup.number()
      .typeError(t('typeError'))
      .required(t('requiredPercent'))
      .min(0, t('positiveError'))
  })

  const handlePriceChange = price => {
    setState(prev => ({ ...prev, price }))
    setState(prev => ({ ...prev, advance: calculateAdvance(price, prev.downPercent) }))
  }

  const handleDownPercent = percent => {
    const total = parseFloat(state.price)
    setState(prev => ({ ...prev, downPercent: percent }))
    setState(prev => ({ ...prev, advance: calculateAdvance(total, percent) }))
  }

  const calculate = e => {
    e.preventDefault()
    schema
      .validate(state, { abortEarly: false })
      .then(() => {
        setErrors({})
        process()
      })
      .catch(err => {
        const errorObj = {}
        err.inner.forEach(er => errorObj[er.path] = er.message)
        setErrors(errorObj)
      })
  }

  const process = () => {
    const loan = state.price - state.advance
    // const monthlyRate = state.interestRate / 100 / 12
    const monthlyRate = Math.pow(1 + state.interestRate / 100, 1 / 12) - 1
    const n = state.years * 12
    const monthlyPayment = monthlyRate === 0
      ? loan / n
      : loan * monthlyRate / (1 - Math.pow(1 + monthlyRate, -n))

    const totalPaid = monthlyPayment * n + state.advance
    const totalInterest = totalPaid - state.price

    const payload = {
      price: state.price,
      years: state.years,
      interestRate: state.interestRate,
      downPercent: state.downPercent,
      advance: state.advance,
      monthlyPayment,
    }

    if (analytics && analytics.track) {
      try {
        analytics.track('calculate_new', payload)
      } catch (e) {}
    }

    resultsHandler({
      rent: monthlyPayment,
      totalUnit: state.price,
      totalCash: state.advance,
      totalLoan: loan,
      totalInterest: totalInterest.toFixed(0),
      totalPaid: totalPaid.toFixed(0),
      minSalary: monthlyPayment * 2.5,
    })
    viewHandler(1)
  }

  return (
    <form className="form-container">
      <div className="form-group w-100">
        <label className="bold" htmlFor="price"> {t('unitPrice')} </label>
        <NumberFormat
          name="price"
          value={state.price}
          thousandSeparator
          onValueChange={num => handlePriceChange(num.floatValue)}
        />
        {errors.price && <p className="error-message">{errors.price}</p>}
      </div>

      <div className="form-group w-30">
        <label className="bold" htmlFor="downPercent"> {t('advancePercent')} </label>
        <NumberFormat
          name="downPercent"
          value={state.downPercent}
          prefix="%"
          onValueChange={num => handleDownPercent(num.floatValue)}
        />
        {errors.downPercent && <p className="error-message">{errors.downPercent}</p>}
      </div>

      <div className="form-group w-30">
        <label className="bold" htmlFor="years"> {t('installmentYears')} </label>
        <input
          name="years"
          type="number"
          value={state.years}
          onChange={e => setState(prev => ({ ...prev, years: e.target.value }))}
        />
        {errors.years && <p className="error-message">{errors.years}</p>}
      </div>

      <div className="form-group w-30">
        <label className="bold" htmlFor="interest"> {t('interestRate')} %</label>
        <NumberFormat
          name="interest"
          value={state.interestRate}
          prefix="%"
          onValueChange={num => setState(prev => ({ ...prev, interestRate: num.floatValue }))}
        />
        {errors.interestRate && <p className="error-message">{errors.interestRate}</p>}
      </div>

      <div className="form-group w-30">
        <label className="bold" htmlFor="advance"> {t('advanceAmount')} </label>
        <NumberFormat
          name="advance"
          value={state.advance.toFixed(2)}
          thousandSeparator
          disabled
        />
      </div>

      <div className="form-group text-center w-100">
        <input type="submit" value={t('calculate')} onClick={calculate} />
      </div>
    </form>
  )
}

export default NewCalculator 