import TableRow from '../components/TableRow'
import { calculateAverage } from '../utils/helpers'
import { t } from '../utils/translate'

const Result = props => {
  const { results, viewHandler, serviceCash, legacyResults = true } = props

  // Build the rows data dynamically to keep the JSX clean and maintainable
  const rows = []

  if (Object.keys(results).length) {
    rows.push({ label: t('unitPrice'), value: results.totalUnit })

    const maintenance = serviceCash ? t('maintenanceDeposit') : ''
    rows.push({
      label: t('cashPaid'),
      ...(legacyResults && { help: t('cashPaidHelp', { maintenance }) }),
      value: results.totalCash,
    })

    const maintenanceStatus = serviceCash ? t('maintenanceExc') : t('maintenanceInc')
    rows.push({
      label: t('totalLoan'),
      ...(legacyResults && { help: t('totalLoanHelp', { maintenanceStatus }) }),
      value: results.totalLoan,
    })

    if (Array.isArray(results.rent)) {
      rows.push({
        label: t('monthlyInstallments'),
        value: results.rent,
        color: 'blue',
      })

      rows.push({
        label: t('averageInstallment'),
        value: calculateAverage(results.rent),
      })
    } else {
      rows.push({
        label: t('installment'),
        value: results.rent,
        color: 'blue',
      })
    }

    if (legacyResults) {
      rows.push({ label: t('minIncome'), value: results.minSalary })
    }
    rows.push({ label: t('totalInterest'), value: results.totalInterest })
    rows.push({ label: t('totalPaid'), value: results.totalPaid })
  }

  return (
    <div>
      {Boolean(rows.length) && (
        <div className="result">
          <table>
            <tbody style={{ verticalAlign: 'baseline' }}>
              {rows.map((row, idx) => (
                <TableRow key={idx} {...row} />
              ))}
            </tbody>
          </table>
          <small> {t('approximateResult')}</small>
        </div>
      )}

      <div className="text-center">
        <input
          onClick={e => {
            e.preventDefault()
            viewHandler(0)
          }}
          type="reset"
          value={t('back')}
        />
      </div>
    </div>
  )
}

export default Result