import React from 'react'

const RadioOption = ({ name, id, value, label, checked, onChange }) => (
  <div>
    <input
      name={name}
      type="radio"
      id={id}
      value={value}
      checked={checked}
      onChange={onChange}
    />
    <label htmlFor={id}> {label} </label>
  </div>
)

export default RadioOption 