export const getOrCreateUserId = () => {
  try {
    let uid = localStorage.getItem('uid');
    if (!uid) {
      if (typeof crypto !== 'undefined' && crypto.randomUUID) {
        uid = crypto.randomUUID();
      } else {
        // Fallback simple uuid
        uid = Math.random().toString(36).substring(2) + Date.now().toString(36);
      }
      localStorage.setItem('uid', uid);
    }
    return uid;
  } catch (e) {
    // localStorage might be unavailable (e.g., privacy mode)
    return undefined;
  }
};

export const identifyUser = analytics => {
  if (!analytics || typeof analytics.id !== 'function') return;
  const uid = getOrCreateUserId();
  if (uid) {
    analytics.id({ uid });
  }
}; 