import { useState } from 'react'
import ReactGA from 'react-ga'
import { useJitsu } from '@jitsu/jitsu-react'
import NumberFormat from 'react-number-format'
import * as yup from 'yup'

import RadioOption from '../components/RadioOption'
import { calculateAdvance, estimateResults } from '../utils/helpers'
import { t } from '../utils/translate'

const errorText = {
  priceError: () => t('priceRangeError'),
  yearsError: () => t('yearsRangeError'),
  percentError: () => t('percentRangeError'),
  typeError: () => t('typeError'),
  positiveError: () => t('positiveError'),
  integerError: () => t('integerError'),
}

const Calculator = props => {
  const { state, setState, setResults, viewHandler } = props

  const [errors, setErrors] = useState({})
  const { analytics } = useJitsu() || {}

  const schema = yup.object().shape({
    price: yup.number()
      .typeError(errorText.typeError())
      .required(t('requiredPrice'))
      .positive(errorText.positiveError())
      .min(100000, errorText.priceError())
      .max(2500000, errorText.priceError()),
    years: yup.number()
      .typeError(errorText.typeError())
      .required(t('requiredYears'))
      .positive(errorText.positiveError())
      .integer(errorText.integerError())
      .min(3, errorText.yearsError())
      .max(30, errorText.yearsError()),
    advPercent: yup.number()
      .typeError(errorText.typeError())
      .required(t('requiredPercent'))
      .positive(errorText.positiveError())
      .min(15, errorText.percentError())
      .max(75, errorText.percentError()),
  });
  
  const handlePriceChange = price => {
    setState(prev => ({ ...prev, price }))
    setState(prev => ({ ...prev, advance: calculateAdvance(price, prev.advPercent) }))
  }

  const handleInitPercent = percent => {
    const total = parseFloat(state.price)
    setState(prev => ({ ...prev, advPercent: percent }))
    setState(prev => ({ ...prev, advance: calculateAdvance(total, percent) }))
  }

  const clear = e => {
    e.preventDefault()
    const confirmed = window.confirm(t('resetFieldsConfirm'))
    if (confirmed) {
      setErrors({})
      setState({
        price: 0,
        years: 0,
        interest: 'i3',
        rentType: 'fixed',
        serviceType: 'in',
        advance: 0,
        advPercent: '',
      })
    }
  }
  
  const submit = e => {
    e.preventDefault()
    ReactGA.event({
      category: 'Button',
      action: 'Calculate',
    })
    schema
      .validate({
          price: state.price,
          years: state.years,
          advPercent: state.advPercent
        },
        { abortEarly: false }
      )
      .then(() => {
        setErrors({})
        const results = estimateResults({ ...state })
        setResults(results)
        viewHandler(1)

        // Track with jitsu
        const payload = {
          price: state.price,
          years: state.years,
          advPercent: state.advPercent,
          interest: state.interest,
          rentType: state.rentType,
          serviceType: state.serviceType,
          advance: state.advance,
        }
        if (analytics && analytics.track) {
          try {
            analytics.track('calculate_old', payload)
          } catch (e) {}
        }
      })
      .catch(errors => {
        const errorList = {}
        const errorsArray = errors.inner || []
        errorsArray.forEach(err => errorList[err.path] = err.message)
        setErrors(errorList)
      })
  }

  return (
    <form className="form-container">
      {/* Price */}
      <div className="form-group w-100">
        <label className="bold" htmlFor="price"> {t('unitPrice')} </label>
        <NumberFormat
          name="price"
          value={state.price}
          thousandSeparator={true}
          onValueChange={num => handlePriceChange(num.floatValue)}
        />
        {Boolean(errors.price !== null) && <p className='error-message'> {errors.price} </p>}
      </div>
      {/* Advance  */}
      <div className="form-group">
        <label className="bold" htmlFor="initPercent"> {t('advancePercent')} </label>
        <NumberFormat
          name="initPercent"
          value={state.advPercent}
          thousandSeparator={true}
          prefix="%"
          onValueChange={num => handleInitPercent(num.floatValue)}
        />
        {Boolean(errors.advPercent !== null) && <p className='error-message'> {errors.advPercent} </p>}
      </div>
      {/* Years  */}
      <div className="form-group">
        <label className="bold" htmlFor="years"> {t('installmentYears')} </label>
        <input
          name="years"
          type="number"
          step="1"
          value={state.years}
          onChange={e => setState(state => ({ ...state, years: e.target.value }))}
        />
        {Boolean(errors.years !== null) && <p className='error-message'> {errors.years} </p>}
      </div>
      <div className="flex w-100">
        {/* Interest */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="interest"> {t('interestSystem')} </label>
          <div className="radio-group flex">
            <RadioOption
              name="interest"
              id="i3"
              value="i3"
              label={t('interest3Declining')}
              checked={state.interest === 'i3'}
              onChange={e => setState(s => ({ ...s, interest: e.target.value }))}
            />
            <RadioOption
              name="interest"
              id="i8"
              value="i8"
              label={t('interest8Declining')}
              checked={state.interest === 'i8'}
              onChange={e => setState(s => ({ ...s, interest: e.target.value }))}
            />
          </div>
        </div>
        {/* Rent Type */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="rentType"> {t('installmentSystem')} </label>
          <div className="radio-group flex">
            <RadioOption
              name="rentType"
              id="fixed"
              value="fixed"
              label={t('installmentFixed')}
              checked={state.rentType === 'fixed'}
              onChange={e => setState(s => ({ ...s, rentType: e.target.value }))}
            />
            <RadioOption
              name="rentType"
              id="inc"
              value="inc"
              label={t('installmentInc')}
              checked={state.rentType === 'inc'}
              onChange={e => setState(s => ({ ...s, rentType: e.target.value }))}
            />
          </div>
        </div>
        {/* Service Type */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="serviceType"> {t('maintenanceSystem')} </label>
          <div className="radio-group flex">
            <RadioOption
              name="serviceType"
              id="in"
              value="in"
              label={t('maintenanceIn')}
              checked={state.serviceType === 'in'}
              onChange={e => setState(s => ({ ...s, serviceType: e.target.value }))}
            />
            <RadioOption
              name="serviceType"
              id="out"
              value="out"
              label={t('maintenanceOut')}
              checked={state.serviceType === 'out'}
              onChange={e => setState(s => ({ ...s, serviceType: e.target.value }))}
            />
          </div>
        </div>
      </div>
      {/* Advance Value  */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> {t('advanceAmount')} </label>
        <NumberFormat
          name="init"
          value={state.advance.toFixed(2)}
          thousandSeparator={true}
          disabled
        />
      </div>
      {/* Loan */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> {t('loan')} </label>
        <NumberFormat
          name="init"
          value={state.serviceType === 'in' ? (state.price + state.price * 0.05 - state.advance) : (state.price - state.advance)}
          thousandSeparator={true}
          disabled
        />
      </div>
      {/* Service 5%  */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> {t('maintenanceDeposit5')} </label>
        <NumberFormat
          name="init"
          value={(state.price * 0.05).toFixed(2)}
          thousandSeparator={true}
          disabled
        />
      </div>
      <div className="form-group w-100">
        {t('addAdminFees', { amount: state.price * 0.015 || 0 })}
      </div>
      <div className="form-group text-center w-100">
        <input
          type="submit"
          value={t('calculate')}
          onClick={e => submit(e)}
        />
        <input
          type="reset"
          value={t('clear')}
          onClick={e => clear(e)}
        />
      </div>
    </form>
  )
}

export default Calculator