export const calculateAverage = arr => {
  if (!Array.isArray(arr) || !arr.length) return 0
  return arr.reduce((total, p) => total + p, 0) / arr.length
}

export const calculateAdvance = (price = 0, percent = 0) => price * (percent / 100)

export const rentSearch = (state, rent) => {
  let remainingYears = parseInt(state.years, 10)
  const interestPercent = state.interest === 'i3' ? 0.03 : 0.08
  let remainingPrice = state.serviceType === 'in'
    ? (state.price + state.price * 0.05 - state.advance)
    : (state.price - state.advance)
  const rentValues = []
  while (remainingYears) {
    remainingPrice += remainingPrice * interestPercent
    remainingPrice -= rent * 12
    remainingYears -= 1
    if (state.rentType === 'inc') {
      rentValues.push(rent)
      rent += rent * 0.07
    }
  }
  return [remainingPrice, rentValues]
}

export const estimateResults = state => {
  let low = 1
  let high = 50000
  let rent = (low + high) / 2
  let [remainingPrice, rentValues] = rentSearch(state, rent)

  while (Math.abs(remainingPrice) > 1) {
    if (remainingPrice > 0) {
      low = rent + 0.1
    } else {
      high = rent - 0.1
    }
    rent = (low + high) / 2
    ;[remainingPrice, rentValues] = rentSearch(state, rent)
  }

  const loan = state.serviceType === 'in'
    ? (state.price + state.price * 0.05 - state.advance)
    : (state.price - state.advance)

  const rentSum = state.rentType === 'inc'
    ? rentValues.reduce((total, p) => total + p * 12, 0)
    : rent * 12 * state.years

  return {
    rent: state.rentType === 'inc' ? rentValues : rent,
    totalUnit: state.price,
    totalCash: state.advance + (state.serviceType === 'in' ? 0 : state.price * 0.05) + state.price * 0.015,
    totalLoan: loan,
    totalInterest: (rentSum - loan).toFixed(0),
    totalPaid: (rentSum + state.advance + state.price * 0.015 + (state.serviceType === 'in' ? 0 : state.price * 0.05)).toFixed(0),
    minSalary: (state.rentType === 'inc' ? rentValues[0] : rent) * 2.5,
  }
} 